.message {
  display: flex;
  align-items: flex-end;
  margin: 0 0 35px 0;
  @media (max-width: 490px) {
    flex-direction: column;
    align-items: flex-start;
  }
}
.author {
  margin: 0 30px 0 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  @media (max-width: 490px) {
    margin: 0 20px 0 0;
    align-self: flex-end;
  }
  & > img {
    flex: 0 0 40px;
    height: 40px;
    border-radius: 50%;
    overflow: hidden;
    @media (max-width: 490px) {
      flex: 0 0 30px;
      height: 30px;
    }
  }
  & > span {
    flex: 1 1 auto;
    display: block;
    font-size: 12px;
    margin: 5px 0 0 0;
  }
}
.messageContainer {
  width: 150px;
  border: 2px solid #6a6868;
  border-radius: 10px;
  display: flex;
  align-items: flex-end;
  &:before {
    content: '';
    width: 0;
    height: 0;
    border-top: 7px solid transparent;
    border-right: 14px solid #6a6868;
    border-bottom: 7px solid transparent;
    margin: 0 0 4px -13px;
    @media (max-width: 490px) {
      display: none;
  }}
  @media (max-width: 490px) {
    width: 90%;
  }
}
.text {
  padding: 5px;
}
