.formControl {
  display: flex;
  flex-direction: column;
  margin: 0 0 15px 0;

  & textarea, input {
    border: none;
    background-color: #E6EDE9;
    margin: 0 0 5px 0;
  }
  & textarea{
    border-radius: 5px;
    width: 200px;
    height: 50px;

  }
  &.error {
    & textarea, input {
      background-color: #ffebe9;
      outline: 2px solid #f60303;
      &:focus {
        background-color: #fff;
        outline: 2px solid #035cd0;
      }
    }
    & span {
      width: 100%;
      border: none;
      font-size: 10px;
      color: #f60303;
      margin: 0 0 0 5px;
      text-align: right;
      text-transform: none;
    }
  }
}
.field {
  display: flex;
  font-size: 12px;
  & span {
    margin: 0 0 0 10px;
  }
}