.wrapper {
  display: grid;
  height: 100%;
  padding: 20px;
  grid-template-rows: 90px 1fr;
  grid-template-columns: 2fr 5fr;
  grid-template-areas: 'title new'
'dialogs messages';
  & > div {
    &:nth-child(1) {
      grid-area: title;
    }
    &:nth-child(2) {
      grid-area: new;
    }
    &:nth-child(3) {
      grid-area: dialogs;
    }
    &:nth-child(4) {
      grid-area: messages;
    }
  }
  @media (max-width: 490px) {
    grid-template-rows: 40px 120px 1fr;
    grid-template-columns: 2fr 5fr;
    grid-template-areas: 'title title'
    'new new'
'dialogs messages';
}
}
.title {
  font-weight: 700;
  font-size: 30px;
  font-family: 'Rowdies';
  color: #6a1b57;
  @media (max-width: 490px) {
    font-size: 22px;
  }
}
.dialogs {
  display: flex;
  margin: 20px 0 0 0;

  &_list {
    width: 100%;
  }
  &:after {
    content: '';
    width: 1px;
    height: 90%;
    margin: 0 20px 0 30px;
    background-color: #000;
    @media (max-width: 490px) {
      margin: 0 15px 0 10px;
    }
  }

}
.dialog {
  margin: 0 0 20px 0;
}
.link, .link_active {
  display: block;
  text-decoration: none;
  font-size: 20px;
  font-weight: bold;
  color: #8a24e8;
  transition: all 0.7s ease 0s;
  @media (max-width: 490px) {
    font-size: 16px;
  }
}
.link_active {
  color: #ec05be;
  font-size: 28px;
  @media (max-width: 490px) {
    font-size: 20px;
  }
}
.messages {
  height: 100%;
  margin: 20px 0 0 0;

  padding: 0;
}
.message {
  display: flex;
  align-items: flex-end;
  margin: 0 0 35px 0;
  &_autor {
    margin: 0 30px 0 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    & > img {
      width: 50px;
      height: 50px;
      border-radius: 50%;
      overflow: hidden;
    }
    & > span {
      display: block;
      font-size: 12px;
      margin: 5px 0 0 0;
    }
  }
  &_text {
    width: 120px;
    border: 2px solid #6a6868;
    border-radius: 10px;
    padding: 5px;
    display: flex;
    align-items: flex-end;
    &:before {
      content: '<';
      margin: 0 0 0 -23px;
      font-size: 30px;
      color: #6a6868;
    }
  }
}